<template>
	<div class="progress-bar" ref="progressBar" @click='progressBarClick'>
		<div class="bar-inner">
			<div class="progress" ref="progress"></div>
			<div class="progress-btn-wrapper"
			    @touchstart.prevent='touchStart'
			    @touchmove.prevent='touchMove'
			    @touchend.prevent='touchEnd'
			>
				<div class="progress-btn" ref="progressBtn"></div>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
	import {prefixStyle} from 'common/js/dom'
	const transform = prefixStyle('transform')
	export default {
		props: {
			percent: {
				type: Number,
				default: 0
			}
		},
		created() {
			this.touch = {}
		},
		watch: {
			percent(newPercent){
				if(newPercent > 0 && !this.touch.initial){
					const barWidth = this.$refs.progressBar.clientWidth
					const offsetWidth = newPercent * barWidth
					this._offset(offsetWidth)
				}
			}
		},
		methods: {
			touchStart(e){
				this.touch.initial = true
				this.touch.startX = e.touches[0].pageX
				this.touch.left = this.$refs.progress.clientWidth
			},
			touchMove(e){
				if(!this.touch.initial){
					return
				}
				const deltaX = e.touches[0].pageX - this.touch.startX
				const offsetWidth = Math.min(this.$refs.progressBar.clientWidth, Math.max(0, this.touch.left + deltaX))
				this._offset(offsetWidth)
			},
			touchEnd(){
				this.touch.initial = false
				this._triggerPercent()
			},
			progressBarClick(e){
				const rect = this.$refs.progressBar.getBoundingClientRect()
				const offsetWidth = e.pageX - rect.left - this.$refs.progressBtn.clientWidth / 2
				this._offset(offsetWidth)
				this._triggerPercent()
			},
			_offset(offsetWidth){
				this.$refs.progress.style.width = offsetWidth + 'px'
				this.$refs.progressBtn.style[transform] = `translate3d(${offsetWidth}px, 0, 0)`
			},
			_triggerPercent(){
				const barWidth = this.$refs.progressBar.clientWidth
				const percent = this.$refs.progress.clientWidth / barWidth
				this.$emit('percentChange', percent)
			}
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .progress-bar
    height: 30px
    .bar-inner
      position: relative
      top: 13px
      height: 4px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          /*left: 7px*/
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-text
          border-radius: 50%
          background: $color-theme
</style>


